/* Donation Progress */
.donation-progress {
  padding: 2rem 0;
  --bar: #0a61ff;
  --track: #fff;
  --track-border: #0a61ff;
  --accent: #c13b5a;
}
.donation-progress .donation-progress__inner {
  max-width: 1200px;
  margin: 0 auto !important;
  padding: 8px 16px 20px;
}
.donation-progress .donation-progress__row {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 10px;
}
.donation-progress .donation-progress__row .donation-progress__raised {
  font-weight: 800 !important;
}
.donation-progress .donation-progress__row .donation-progress__goal {
