  .impact-stats__grid.impact-stats__grid--cols-2 > .impact-stats__item:last-child:nth-child(2n+1) {
    grid-column: 1/-1;
    justify-self: center;
    text-align: center;
    width: fit-content;
    max-width: 100%;
  }
  .impact-stats__grid {
    /* 3 columns: total count  1 (mod 3)  put last item in middle column */
  }
  .impact-stats__grid.impact-stats__grid--cols-3 > .impact-stats__item:last-child:nth-child(3n+1) {
    grid-column: 2; /* middle column */
