/* ===== Where Your Gift Goes  2-row layout per card ===== */
.gift-goes {
  padding: 3rem 0;
  /* container + heading (keep what you like from earlier) */
}
.gift-goes .gift-goes__inner {
  max-width: 1100px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}
.gift-goes .gift-goes__heading {
  text-align: center;
  font-weight: 900;
  margin: 0 0 20px;
}
.gift-goes .gift-goes__heading .is-accent {
  color: var(--accent);
}
.gift-goes {
  /* GRID of cards (unchanged) */
}
.gift-goes .gift-goes__grid {
  display: grid;
  gap: 18px;
  grid-template-columns: 1fr;
}
@media (min-width: 600px) {
  .gift-goes .gift-goes__grid {
    grid-template-columns: 1fr 1fr;
  }
}
.gift-goes {
  /* === CARD === */
}
.gift-goes .gift-goes__card {
  background: #fff;
  border-radius: 12px;
  padding: 18px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.06);
  /* ROW 1: title full width
     ROW 2: 50/50  list+cta on left, map on right */
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "title title" "list  map" "cta   map";
  column-gap: 24px;
  row-gap: 12px;
}
.gift-goes {
  /* let headers children (title + map) participate directly */
}
.gift-goes .gift-goes__card-head {
  display: contents;
}
.gift-goes {
  /* Title = first row, full width */
}
.gift-goes .gift-goes__title {
  grid-area: title;
  margin: 0;
  font-size: 20px;
  font-weight: 800;
  line-height: 1.2;
  color: #11151a;
}
.gift-goes {
  /* Map = right column */
}
.gift-goes .gift-goes__mapwrap {
  grid-area: map;
  justify-self: end;
  align-self: start;
  width: clamp(120px, 26vw, 180px);
  height: clamp(96px, 20vw, 140px);
  border-radius: 8px;
  overflow: hidden;
}
.gift-goes .gift-goes__mapwrap img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}
.gift-goes {
  /* Locations = left column */
}
.gift-goes .gift-goes__list {
  grid-area: list;
  margin: 4px 0 10px 18px;
  padding: 0;
}
.gift-goes .gift-goes__list li {
  font-size: 18px;
  line-height: 1.35;
  margin: 0 0 6px;
  color: #11151a;
}
.gift-goes {
  /* CTA = left column under the list */
}
.gift-goes .gift-goes__cta {
  grid-area: cta;
  margin-top: 4px;
}
.gift-goes {
  /* Button (reuse your palette) */
}
.gift-goes .btn-cta {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  border-radius: 10px;
  background: var(--accent);
  color: #fff;
  text-decoration: none;
  font-weight: 700;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.1), 0 8px 16px rgba(0, 0, 0, 0.12);
  transition: transform 0.1s ease, filter 0.2s ease;
}
.gift-goes .btn-cta:hover {
  filter: brightness(1.05);
}
.gift-goes .btn-cta:active {
  transform: translateY(1px);
}
.gift-goes .btn-cta__icon {
  font-size: 18px;
  line-height: 1;
  transform: translateY(-1px);
}
.gift-goes .btn-cta__label {
  font-size: 16px;
  line-height: 1;
}
.gift-goes {
  /* Bottom background image (keep yours) */
}
.gift-goes .gift-goes__bg {
  position: absolute;
  inset-inline: 0;
  bottom: -8px;
  height: min(320px, 45vw);
  background-image: var(--bg-bottom);
  background-size: contain;
  background-position: bottom center;
  background-repeat: no-repeat;
  pointer-events: none;
  z-index: 0;
}
@media (max-width: 767.98px) {
  .gift-goes .gift-goes__bg {
    background-image: var(--bg-bottom-m, var(--bg-bottom));
    height: min(260px, 60vw);
  }
